import { Component,NgZone, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NavController, Platform } from '@ionic/angular';
import { iif } from 'rxjs';
import { UploadingService } from 'src/app/imageCapture/uploading.service';
import { ApiservicesService } from 'src/app/provider/apiservices.service';
import { DataTransferService } from 'src/app/services/data-transfer.service';
import { LocalStorageService } from 'src/app/services/local-storage.service';
import { URLS } from 'src/assets/constant';
import { Camera, CameraOptions } from "@ionic-native/camera/ngx";
@Component({
  selector: 'app-idea',
  templateUrl: './idea.page.html',
  styleUrls: ['./idea.page.scss'],
})
export class IdeaPage implements OnInit {
  CategoryData: any=[];
  autoId: any;
  suggestions: string='';
  tab1PageData:any;
  attachData:any;
  showFilterDiv:boolean = false;
  mediaType: any="0";
  pdfData: string;
  categoryVal: string;
  constructor(private router:Router,private sendData:DataTransferService,private camera:Camera,
    private zone:NgZone,private localStorage:LocalStorageService,private apiservices:ApiservicesService,private navctrl:NavController) {
      this.tab1PageData=this.sendData.alldata; 
      console.log('val', this.tab1PageData);
  
    }
    selectOption(ev){
      this.autoId=ev.detail.value;
      console.log('val ev', ev);

    }
    attachment(event){
      console.log('event1==>', event);
      // var formData = new FormData();
      if(event.target.files){
        const file = event.target.files[0];
        console.log("file",file)
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
            // console.log("rrrrrrrrr",reader.result);
            this.attachData = reader.result;
            this.showFilterDiv = false;
            console.log("file",file.type.split("/"));
            let extenstion=file.type.split("/");                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              
            console.log('this.currentObj==>',   this.attachData);
            if(extenstion[1]=='pdf'){
              this.mediaType="4";
              this.pdfData=file.name;
              console.log('this.currentObj==>',  this.pdfData);
            //  this.pdfData= window.URL.createObjectURL(this.attachData);
            }else{
              this.mediaType="1";
              console.log('this.currentObj==>',   this.mediaType);
            }
            // formData.append("file", this.files);
            // console.log("files : ", this.files)
           console.log('this.currentObj==>',   this.attachData);
          // this.currentObj.answer= reader.result;
         
        };
  
      }
    }

  ngOnInit() {
  this.categoryList();
  }
  gotoHistory(){
    this.router.navigate(['/idea-history']);
  }

  IdeaSubmit(){
    this.zone.run(()=>{
      this.apiservices.showLoader();
      this.localStorage.getStorage().then(stored=>{
        let apiKey={  
          "employee_id":stored.employeeID,
          "category_id":this.autoId,
          "citation":this.suggestions,
          "media_type":this.mediaType,
          "media":this.attachData
      };
      let copy_apiKey={
        "employee_id":stored.employeeID,
        "category_id":this.autoId,
        "citation":this.suggestions,
        "media_type":this.mediaType,
        "media":this.attachData
      };
      console.log("resend OTP by Voice Call, api key", apiKey);
      this.apiservices.apiCallWithLoginTokenComment(URLS.ideaBoxListApi, apiKey,copy_apiKey).subscribe((result) => {
        console.log("resend OTP by Voice Call, api response==",result);
        this.apiservices.hideLoader();
        if(result.status==1 || result.status=='1'){
          this.suggestions='';
          this.attachData='';
          this.pdfData='';
          this.mediaType='';
          this.categoryVal='';
          // this.sendData.alldata={"apiRes":result ,"formData":apiKey};
          this.apiservices.showToastMessage(result.message, 'top', 3000, 'greenBg');
          // this.navctrl.pop();
          // this.router.navigate(['/qualification-priview']);
        }else{
          this.apiservices.showToastMessage(result.message, 'top', 3000, 'redBg');
        }
      },err=>{
        this.apiservices.hideLoader();
        this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
      });
      });
  });


  }

  openActionDiv(){
    this.zone.run(()=>{
      console.log("show filter1==", this.showFilterDiv);
      if (this.showFilterDiv) {
        console.log("show filter2==", this.showFilterDiv);
        this.showFilterDiv = false;
      }else{
        console.log("show filter3==", this.showFilterDiv);
        this.showFilterDiv=true;
      }
    });
  }

  remove(){
    this.pdfData='';
    this.attachData='';
  }
  opencam() {
    const options: CameraOptions = {
      quality: 30,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
    };
    this.camera.getPicture(options).then((imageData) => {
      console.log('imageData', imageData);
        this.attachData ='data:image/jpeg;base64,'+ imageData;
        this.showFilterDiv = false;
        this.mediaType="1";
      },
      (err) => {
        // Handle error
      }
    );
  }
  categoryList(){
    this.zone.run(()=>{
      this.localStorage.getStorage().then(stored=>{
      let apiKey={  
        "employee_id":stored.employeeID
    };
      
      console.log("resend OTP by Voice Call, api key", apiKey);
      this.apiservices.apiCallWithLoginToken(URLS.ideaCategoryListApi, apiKey).subscribe((result) => {
        console.log("resend OTP by Voice Call, api response==",result);
        if(result.status==1 || result.status=='1'){
          this.CategoryData=result.data;
        }else{
          this.apiservices.showToastMessage(result.message, 'top', 3000, 'redBg');
        }
      },err=>{
        this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
      });
    
  });
});

  }
  

}
